module.exports.genhtml = (getter, getterdate, jobother, pay, cut, pay_g, paybefore, pay_p) => {
  return new Promise((resolve, reject) => {
    let bobypay = ''
    for (var i in getterdate) {
      bobypay = bobypay + `   <tr>
            <td>${getterdate[i].date}</td>
            <td>

                ${getterdate[i].start}
            </td>
            <td>

                ${getterdate[i].end}
            </td>
            <td>${getterdate[i].cut}</td>
            <td>${getterdate[i].hour}</td>
            <td>${getterdate[i].pay}</td>
            
        </tr>
      `
    }

    let bobypay_g = ''
    for (var i in jobother) {
      bobypay_g = bobypay_g + ` <tr>
            <td>
              ${jobother[i].name} 
            </td>
            <td>
              ${jobother[i].pay} 
            </td>

          </tr>
      `
    }

    let bobypay_p = ''
    for (var i in paybefore) {
      bobypay_p = bobypay_p + ` <tr>
            <td>
              ${paybefore[i].name} 
            </td>
            <td>
              ${paybefore[i].pay} 
            </td>

          </tr>
      `
    }

    let hhml = ` 
            <div class=" " style="width: 100%;">
                <h5></h5>
              
                <table style="width: 100%;font-size:12px;margin-top:20px;">
                
                <tbody>
                 
 
                <tr>
                <th scope="col">  : ${getter.employee.employees_name}</th>
                <th scope="col">  : ${getter.positions.position_name}</th>
                </tr>

                <tr>
                <th scope="col"> (/) :
                ${getter.employee.pay_full_time}/${getter.employee.pay_half_time}
                </th>
                <th scope="col"> () : ${getter.employee.cut_late_for_work} /.</th>
                </tr>

                <tr>
                <th scope="col"> : ${getter.positions.full_time_in}-${getter.positions.full_time_late}/${getter.positions.full_time_out}</th>
                <th scope="col"> : ${getter.positions.half_time_in}-${getter.positions.half_time_late}/${getter.positions.full_time_out} </th>
                </tr>

                </tbody>

                </table>
                <h6 style="font-size:15px;margin-top:20px;"></h6>
                <table style="width: 100%;font-size:12px;margin-top:20px;">
                    <thead>
                        <tr>
                            <th scope="col"></th>
                            <th scope="col"></th>
                            <th scope="col"></th>
                            <th scope="col">()</th>
                            <th scope="col">.</th>
                            <th scope="col">()</th>
        
                        </tr>
                    </thead>
                    <tbody>
                     
${bobypay}
<tr>
<th scope="col"></th>
<th scope="col"></th>
<th scope="col"></th>
<th scope="col">${cut}</th>
<th scope="col"></th>
<th scope="col">${pay}</th>

</tr>
                    </tbody>
                </table>
        
                <div style="width: 50%;float: left;padding: 10px;">
                 <h6 style="font-size:15px;margin-top:20px;"></h6>
                <table style="width: 100%;font-size:12px;margin-top:20px;">
                  <thead>
                    <tr>
                      <th scope="col"></th>
                      <th scope="col">()</th>
                      
                    </tr>
                  </thead>
                  <tbody>
                   ${bobypay_g}
              
                    <tr>
                      <th scope="col"></th>
        
                      <th scope="col">${pay_g}</th>
        
                      
                    </tr>
                  </tbody>
                </table>
                </div>
               
                <div style="width: 50%;float: left;padding: 10px;">
                <h6 style="font-size:15px;margin-top:20px;"></h6>
               <table style="width: 100%;font-size:12px;margin-top:20px;">
                 <thead>
                   <tr>
                     <th scope="col"></th>
                     <th scope="col">()</th>
                     
                   </tr>
                 </thead>
                 <tbody>
                  ${bobypay_p}
             
                   <tr>
                     <th scope="col"></th>
       
                     <th scope="col">${pay_p}</th>
       
                     
                   </tr>
                 </tbody>
               </table>
               </div>

                <table style="width: 100%; margin-top:20px;">
                
                <tbody>
                 
 
                <tr>
                <th scope="col"> </th>
                <th scope="col"> : ${Number(pay) + Number(pay_g) - Number(pay_p)} </th>
                </tr>
                </tbody>
          
            </div>
       `


    resolve(hhml)
  });
}